/* App ----------*/
:root {
    --main-bg-color: #000000;
    --main-fg-color: rgb(255, 255, 255);
    --accent-color: #292929;
  }

#root, .app {
    height: 100vh;
    max-height: 100vh;
}
body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    
}

body, .search, .search::placeholder{
    background-color: var(--main-bg-color);
    color: var(--main-fg-color);
}

a:link, a:visited,a:hover,a:active{
    color: var(--main-fg-color);
}
/* clicking on any child activates this div's onClick */
.child-clicks-this > * {
    pointer-events: none;
}

.app {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 35px 1fr 40px;
    overflow: hidden;
}

.page {
    margin: 15px;
    overflow-y:auto;
    -ms-overflow-style: none;
}
.page::-webkit-scrollbar {
    display: none;
  }

/* Header ----------*/
.header, .header-search, .header-review {
    font-weight: 600;
    /* box-shadow: 0 1px 2px 0; */
    padding-top: 5px;
    height: 35px;
    width: 100%;
    background-color: var(--accent-color);
}
.header, .header-search {
    display: grid;
    grid-template-columns: 0.2fr 1fr 0.15fr;
}

/* .header-search {
    display: grid;
    grid-template-columns: 0.2fr 1fr 1fr;
} */

.header-review {
    display: grid;
    grid-template-columns: 0.05fr 1fr 0.1fr;
}

.header h1, .header-review h1 {
    font-weight: 400;
    font-size: 27px;
    margin-block-start: 0;
    margin-block-end: 0;
}

.search {
    border-style: none;
    /* margin-block-start: 0.1em; */
    height: 30px;
     width: 100%; 
    font-size: 19px;
    outline-style: none;
}

.search::placeholder {
    opacity: 85%;
}

.left-icon-div {
    text-align: center;
}
.left-icon, .right-icon {
    /* margin-block-start: 0.2em; */
    height: 30px;
    width: 30px;
}

/* Fixture Table */
.fixture-table {
    margin-top: 15px;
    margin-left: 10px;
    margin-right: 10px;
}

.fixture-row, .remove-button, .remove-button-x, .quantity-input {
    vertical-align: middle;
    padding: 0;
    margin: 0;
}
.fixture-row {
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    align-self: center;
    font-size: 1.3em;
    display: grid;
    grid-template-columns: 1fr 0.15fr 0.1fr;
    margin-bottom: 20px;
}

.fixture-in-row-details {
    font-size: 14px;
    font-family: 'Open Sans', sans-serif;
}
.remove-button, .quantity-input input {
    background-color: var(--main-fg-color);
    color: var(--main-bg-color);
}

.remove-button {
    border-style: none;
    border-width: 0;
    border-radius: 50%;
    padding: 0;
    height: 20px;
    width: 20px;
    margin: 0;
}

.remove-button-x {
 margin: 0;
 width: 100%;
 height: 100%;
}

.quantity-input input {
    width: 30px;
    height: 30px;
    padding: 0px;
    border-radius: 10px;
    border-style: none;
    border-width: 1px;
    outline-style: none;
    text-align: center;
    vertical-align: middle;
    font-size: 1.1em;
}
.quantity-input input::placeholder{
    color: var(--main-bg-color);
    /* font-size: 2em; */
    margin:0;
    padding: 0;
}
.quantity-input input:focus::placeholder {
    color: transparent;
  }



/* Footer ----------*/
#footer {
    padding-top: 5px;
    display: grid;
    grid-template-columns: 0.2fr 1fr 0.2fr 1fr 0.2fr;
    /* position: fixed;
    left: 0;
    bottom: 0;
    width: 100%; */
    text-align: center;
    /* box-shadow: 0 -1px 2px 0; */
    background-color: var(--accent-color);
}
.footer-icon {
    height: 32px;
    width: 32px;
}


/* Fixture Page */
.fixture-details {
    font-size: 1.3em;
    margin-top: 15px;
    margin-left: 20px;
    margin-right: 20px;
}

.fixture-details div {
    margin-bottom: 5px;
}
#fixture-detail-backarrow {
    height: 32px;
    width: 32px;
}


/* Review/Summary page */
.page h2 {
    font-weight: 400;
}
.totals {
    text-align:center;
    margin: 5px;
    display: grid;
    grid-template-columns:  1fr  1fr;
    grid-template-rows:  1fr  1fr;
    grid-template-areas:
        "watts kg"
        "amps amps";
}

.review-total {
    font-size: 3.5em;
}

.review-unit {
    font-size: 1.5em;
}
.review-unit sup {
    font-size: 0.5em;
}

.review-power {
    grid-area: watts;
}

.review-weight {
    grid-area: kg;
}

.review-amps {
    grid-area: amps;
}
